import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RxLiveAgentSettingsService {
  constructor(private httpClient: HttpClient) {}

  private chatbotConfigurationUrl = '/api/rx/application/chat/chatbotconfiguration';

  getAvailableTopics(): Observable<string[]> {
    return this.httpClient.get<string[]>(`${this.chatbotConfigurationUrl}/virtualchattopics`);
  }
}
